<template>
  <div class="orderList">
    <div class="navList">
      <div style="height:59px;">
        <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
          <van-tab v-for="(item,index) in orderClassifyList" :key="index" :title="item.label"></van-tab>
        </van-tabs>
      </div>
      <div class="mid">
        <div class="list mescroll" id="list">
          <div class="list-data" id="listData">
            <div class="item" v-for="(item,index) in list" :key="index">
              <div class="item-top flex" @click="toDetail(item.orderCode)">
                <div class="No flex-2">订单编号:{{item.orderno}}</div>
                <div class="status" :style="item.orderStatus == 1 || item.orderStatus == 3?'color:#eb5252':item.orderStatus == 4?'color:#333':'color:#999'">{{$utils.getText(item.orderStatus)}}</div>
              </div>
              <div>
                <div class="item-goods flex" v-for="(item1,index1) in item.scEcomOrdersDetailList" :key="index1" @click="toDetail(item.orderCode)">
                  <div class="goods-pic">
                    <img :src="item1.scEcomGoods?$utils.dealImg(item1.scEcomGoods.gimg):''" />
                  </div>
                  <div class="flex-2">
                    <p class="title">{{item1.scEcomGoods?item1.scEcomGoods.gname:''}}</p>
                    <div class="price">
                      ¥
                      <span class="money">{{item1.scEcomGoods?item1.scEcomGoods.goutPrice.toFixed(2):''}}</span>
                      <span class="num">×{{item1.gOrderCount}}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="orderInfo" @click="toDetail(item.orderCode)">
                <div class="price">共{{item.shopCount}}件商品{{item.oOrderYunFei?`，运费￥${item.oOrderYunFei.toFixed(2)}`:''}} 总计：¥{{item.oOrderMoneyShifu.toFixed(2)}}</div>
              </div>
              <div class="item-bottom">
                <div class="btn btnPay" v-if="item.orderStatus  == 3" @click="handle(item,1)">确认收货</div> 
                <div class="btn btnPay" v-if="item.orderStatus  == 1" @click="handle(item,3)">去付款</div>
                <div class="btn" @click="toDetail(item.orderCode)">订单详情</div>
                 <img v-if="item.orderStatus == 2" @click="showQrcode(item)" class="qrcode" src="../../assets/img/icon/qrcode.png" alt />
                <div class="btn" v-if="item.orderStatus  == 5" @click="handle(item,5)">删除订单</div>  
                 <div class="btn" v-if="item.orderStatus  == 1" @click="handle(item,6)">取消订单</div> 
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="show">
      <div class="popup-mid" @click="show = false">
        <img :src="qrcode" alt />
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Tab, Tabs, Dialog, Popup } from "vant";
import { getOrderList, getQrcode } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Popup.name]: Popup
  },
  data() {
    return {
      active: 0,
      activeValue: "",
      orderClassifyList: [
        { label: "全部", value: "" },
        { label: "待付款", value: "1" },
        { label: "待发货", value: "2" },
        { label: "待收货", value: "3" },
        { label: "已完成", value: "5" }
      ],
      mescroll: null,
      list: [],
      orderId: "",
      orderNo: "",
      show: false,
      qrcode: ""
    };
  },
  methods: {
    onClick(name, title) {
      this.activeValue = this.orderClassifyList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    toDetail(id) {
      this.$router.push({
        name: "orderDetail",
        query: { id: id }
      });
    },
    showQrcode(item) {
      getQrcode({
        url:
          this.$config.http +
          "/wx/#/closeOrderDetail?type=1&id=" +
          item.orderCode +
          "&orderCode=" +
          item.orderCode
      }).then(res => {
        if (res.code == 0) {
          this.qrcode = res.data.qrCode;
          this.show = true;
        }
      });
    },
    handle(item, type) {
      this.orderId = item.orderCode;
      this.orderNo = item.orderno;
      let that = this;
      switch (type) {
        case 1:
          //确认收货
          Dialog.confirm({
            title: "提示",
            message: "是否确定收货？"
          })
            .then(() => {
              // on confirm
              that.delivery();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 2:
          //查看物流
          this.toLogistics();
          break;
        case 3:
          //去付款
          Dialog.confirm({
            title: "提示",
            message: "是否确定付款？"
          })
            .then(() => {
              // on confirm
              that.makePreOrder();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 4:
          //联系客服
          break;
        case 5:
          //删除订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定删除？"
          })
            .then(() => {
              // on confirm
              that.delete();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 6:
          //取消订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定取消？"
          })
            .then(() => {
              // on confirm
              that.cancel();
            })
            .catch(() => {
              // on cancel
            });
          break;
        default:
          console.log(type);
      }
    },
    delivery() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      deliveryGoods({
        order_id: this.orderId
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已收货");
            this.mescroll.resetUpScroll();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    delete() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      deleteOrder({
        order_id: this.orderId
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已删除");
            this.mescroll.resetUpScroll();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    cancel() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      cancelOrder({
        id: this.orderId
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已取消");
            this.mescroll.resetUpScroll();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    makePreOrder() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      weixinPay({
        out_trade_no: this.orderNo,
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        oauth_openid:
          sessionStorage.getItem("openId") || "ofDvU6RWRq_iQobXT-vyJx1yJ8Dw"
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            if (!res.data.appId) {
              this.$toast("支付成功", "middle");
              this.mescroll.resetUpScroll();
              // this.$router.push({
              //   name: "payResult",
              //   query: { id: this.detail.id }
              // });
            } else {
              this.weChatPay(
                res.data.appId,
                res.data.timeStamp,
                res.data.nonceStr,
                res.data.packageValue,
                res.data.signType,
                res.data.sign
              );
            }
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          loading.clear();
          console.log(err);
          this.$toast({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    weChatPay(appId, timeStamp, nonceStr, packageValue, signType, sign) {
      console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              $this.$toast("支付成功", "middle");
              $this.mescroll.resetUpScroll();
              // setTimeout(() => {
              //   $this.$router.replace({
              //     name: "payResult",
              //     query: { id: $this.detail.id }
              //   });
              // }, 500);
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              $this.$toast("取消支付", "middle");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              $this.$toast("支付失败", "middle");
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        orderStatus: this.activeValue
      };
      getOrderList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.msg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "我的订单";
    this.active = this.$route.query.index || 0;
    this.activeValue = this.orderClassifyList[this.active].value;
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
/deep/.van-tabs__nav{
  // background-color: #253238;
}
/deep/.van-tabs__wrap{
  overflow: inherit;
}
.orderList {
  height: 100%;
  .navList {
    height: 100%;
  }
  .mid {
    height: calc(100% - 59px);
  }
  .list {
    // padding-bottom: 40px;
    border-top: 1pt solid #efefef;
    box-sizing: border-box;
    .item {
      padding: 15px 10px;
      border-bottom: 1pt solid #efefef;
      background: #fff;
      .flex {
        display: flex;
        .flex-1 {
          flex: 1;
        }
        .flex-2 {
          flex: 2;
          position: relative;
        }
      }
      .item-top {
        font-size: 15px;
        line-height: 20px;
        .No {
          color: #333;
        }
      }
      .item-goods {
        margin: 15px 0;
        .goods-pic {
          width: 100px;
          height: 100px;
          margin-right: 10px;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 5px 0 10px;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
            color: #999;
            margin-left: 10px;
          }
        }
      }
      .orderInfo {
        font-size: 15px;
        .price {
          font-size: 14px;
          text-align: right;
          color: #666;
        }
      }
      .item-bottom {
        text-align: center;
        overflow: hidden;
        padding: 15px 0;
        .qrcode {
          float: right;
          width: 20px;
          height: 20px;
          margin-right: 5px;
          margin-top: 5px;
        }
        .btn {
          width: 80px;
          color: #666;
          border: 1pt solid #e5e5e5;
          float: right;
          height: 30px;
          line-height: 30px;
          border-radius: 5px;
          font-size: 13px;
          margin-left: 5px;
        }
        .btnPay {
          background: #fff;
          color: $base-color;
          border: 1pt solid $base-color;
        }
      }
    }
  }
  .van-popup {
    background: rgba(0, 0, 0, 0.3);
  }
  .popup-mid {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    img {
      width: 80vw;
      height: 80vw;
      display: block;
      margin: calc((100vh - 80vw) / 2) auto;
    }
  }
}
</style>
